@use "../../scss/" as *;

.body-all {
    width: 100%;
    margin: auto;
    background-color: #E6D5B8;
}

@media only screen and (min-width: 821px) {
    .body-all {
        width: 50%;
        margin: auto;
        background-color: #E6D5B8;
    }

    .content-view {
        padding: 32px;
    }
  }

.user-name {
    font-weight: 600;
    font-size: xx-large;
    color: #F0A500;
    text-align: center;
    margin-bottom: 8px;
}

.avatar-view {
    width: 200px;
    height: 200px;
    margin: auto;
    margin-top: 32px;
    margin-bottom: 8px;
    border-radius: 100px;
    border: 2px solid #F0A500;
    .avatar {
        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
}

.user-title {
    margin-bottom: 4px;
    font-size: small;
    color: #E6D5B8;
    text-align: center;
}

.section-view {
    .title {
        font-weight: 600;
        font-size: x-large;
        color: #1B1A17;
        margin-top: 16px;
        margin-bottom: 8px;
    }

    .text {
        color: #1B1A17;
        font-weight: 300;
        font-size: medium;
        padding-bottom: 8px;
        line-height: 1.4;
        padding-left: 16px;
    }

    .text-no-padding-left {
        padding-left: 0px;
    }

    .highlight {
        color: #1B1A17;
        font-style: italic;
        font-weight: 600;
    }

    .summary {
        color: #1B1A17;
        font-style: italic;
        font-size: medium;
        line-height: 1.5;
        margin-bottom: 16px;
        background-color: #F0A500;
        padding: 8px 16px 8px 16px;
        box-shadow: 5px 5px #1B1A17;
        border: 1px solid #1B1A17;
    }

    .project-view {
        background-color: #F0A500;
        margin-bottom: 16px;
        padding: 10px;
        box-shadow: 5px 5px #1B1A17;
        border: 1px solid #1B1A17;
        .name {
            font-weight: 600;
            font-size: large;
        }

        .description {
            line-height: 1.2;
            padding-left: 8px;
            padding-top: 4px;
        }

        .job-title {
            margin-top: 16px;
            font-weight: 600;
            font-size: medium;
        }

        .job-content {
            padding-left: 8px;
            padding-top: 8px;
            font-weight: 300;
        }
    }

    .center-text {
        text-align: center;
    }
}

.bottom-view {
    background-color: #1B1A17;
    width: 100%;
    padding: 32px;
    border-top: 3px solid #F0A500;
    height: 64px;

    .text {
        color: #F0A500;
        font-weight: 300;
        font-size: medium;
        padding-bottom: 16pxpx;
        text-align: center;
    }
}

.top-view {
    background-color: #1B1A17;
    width: 100%;
    padding: 32px;
    border-bottom: 3px solid #F0A500;

    .button {
        border-radius: 4px;
        background-color: #F0A500;
        font-size: medium;
        padding: 16px 16px 16px 16px;
        color: #1B1A17;
        margin-top: 16px;
        margin-right: 8px;
        box-shadow: 5px 5px #E45826;
    }

    .top-title {
        font-weight: 600;
        margin-bottom: 4px;
        font-size: x-large;
        margin-left: 8px;
        color: #F0A500;
        //E45826
    }
}

.content-view {
    padding: 16px;
}